import Joi from 'joi'
import chalk from 'chalk'

const defaultValue = {
	isolation: false,
	reset: {
		"display": "block",
		"boxSizing": "borderBox",
	},
}
const propType = {
	isolation: Joi.boolean().required(),
	reset: Joi.object().required()
}

export default (css, getDefaultValue = false) => {

	if (getDefaultValue) {
		return defaultValue
	}
	const {error, value} = Joi.validate(css, propType)

	if (error !== null) {
		console.error(error.message)
		console.log(chalk.bold.yellow(`Current value: "${JSON.stringify(css, null, 2)}"`))
		console.log(chalk.bold.cyan(`We are using: "${JSON.stringify(defaultValue, null, 2)}"`))
		return defaultValue
	}

	return value
}